/*==============================================================================
  @Docs Demos - scss/custom/_docs-demos.scss
==============================================================================*/
.demo {
  margin: 1.5em -1.5em;
  padding: 0.5em 1.5em;
  overflow: auto;
  border-top: 3px solid #F5F5F5;
  border-bottom: 3px solid #F5F5F5;
}

.demo .box {
  width: 50px;
  height: 50px;
  margin: 0 auto;
  color: #fff;
  background: #2196F3;
  border-radius: 3px;
}

@media (min-width: 760px) {
  .demo {
    margin: 1.5em 0;
    padding: 2em 3em;
    border: 3px solid #F5F5F5;
    border-radius: 3px;
  }
}

/**
 * Demo Naked
 */
.demo-naked {
  border: none;
  margin: 2em 0;
  padding: 0;
}

/**
 * Demo Alternate Styles
 */
.demo.demo-alt {
  padding: 1.5em;
}

@media (min-width: 760px) {
  .demo.demo-alt {
    padding: 3em;
  }
}

/**
 * Vertical Center
 */
.demo.demo-vertical-center {
  height: 200px;
}

.demo.demo-vertical-center .box {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

/**
 * Text Truncate
 */
.demo.demo-add-text-truncate .box {
  width: 150px;
  height: auto;
  padding: 1em 1.5em;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/**
 * Text Hide
 */
.demo.demo-logo-image .logo {
  display: block;
  margin: 1.5rem auto;
  padding: 0;
  border: 0 none;
  background: url("../img/baseweb-hori-black.svg") no-repeat left center;
  background-size: 187px 40px;
  width: 187px;
  height: 40px;
  font-size: 0;
  line-height: 0;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

/**
 * Triangle
 */
.demo.demo-triangle {
  text-align: center;
}

.demo.demo-triangle:after {
  content: '';
  display: table;
  clear: both;
}

.example-triangle-1, .example-triangle-2, .example-triangle-3 {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border: 0 none;
  border-style: solid;
  border-color: transparent;
}

.example-triangle-1 {
  border-color: transparent;
  border-width: 14px 14px 14px 0;
  border-right-color: #2196F3;
}

.example-triangle-2 {
  border-color: transparent;
  border-width: 14px 14px 0 14px;
  border-top-color: #9C27B0;
}

.example-triangle-3 {
  border-color: transparent;
  border-width: 14px 0 14px 14px;
  border-left-color: #F44336;
}

/**
 * Grid System
 */
.demo-grid {
  margin: 1.5rem 0;
}

.demo-grid .col {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.demo-grid .col p {
  margin: 0;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.05);
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 0.875em;
  line-height: 1.5em;
  text-align: center;
  border-radius: 3px;
}

/**
 * Semantic Grid Demo
 */
.demo-grid-semantic .demo-wrapper,
.demo-grid-semantic .demo-content,
.demo-grid-semantic .demo-aside,
.demo-grid-semantic .demo-article {
  background: rgba(33, 150, 243, 0.1);
}

.demo-grid-semantic .demo-wrapper p,
.demo-grid-semantic .demo-content p,
.demo-grid-semantic .demo-aside p,
.demo-grid-semantic .demo-article p {
  margin: 0;
  padding: 1rem;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 0.875em;
  line-height: 1.5em;
  text-align: center;
}

.demo-grid-semantic .demo-aside p,
.demo-grid-semantic .demo-article p {
  margin: 0.75rem 0;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 3px;
}

.demo-grid-semantic .demo-wrapper {
  width: 100%;
  min-width: none;
  max-width: 75rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.demo-grid-semantic .demo-wrapper:after {
  content: '';
  display: table;
  clear: both;
}

.demo-grid-semantic .demo-content {
  width: auto;
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

