  margin: 2em 0;
  display: flex;
  justify-content: center;
}

.social-links a {
  padding: 0.25em 0.75em;
  background: #212121;
  border-radius: 3px;
  font-size: 0.9em;
  line-height: 1.5em;
  color: #fff;
  border-bottom: none;
}

.social-links a:hover {
  opacity: 0.7;
}

.social-links a + a {
  margin-left: 0.25rem;
}

.social-links .social-facebook {
  background: #3b5998;
}

.social-links .social-twitter {
  background: #00aced;
}

.social-links .social-google {
  background: #dd4b39;
}

.social-links .icon {
  font-size: 1.375em;
  margin-top: 0;
  margin-bottom: -0.2em;
  fill: currentColor;
  stroke: currentColor;
  stroke-width: 0;
}

@media (min-width: 760px) {
  .social-links {
    margin-top: 4em;
  }
  .social-links a {
    padding: 0.5em 1em;
    font-size: 1em;
  }
  .social-links a + a {
    margin-left: 0.5em;
  }
}

/**
 * Page Navigation
 */
.page-nav {
  margin: 2em 0;
}

.page-nav a {
  position: relative;
  display: block;
  margin: 1em 0;
  padding: 1em 1.5em;
  background: #fff;
  border: 2px solid #2196F3;
  border-radius: 3px;
  color: #2196F3;
  transition: none;
}

.page-nav a:hover {
  background: #2196F3;
  border-color: #2196F3;
  color: #fff;
}

.page-nav .icon {
  float: left;
  margin: 0.125em;
  font-size: 1.5em;
  vertical-align: top;
}

.page-nav span {
  display: block;
  padding-left: 3em;
  line-height: 2em;
}

@media (min-width: 760px) {
  .page-nav {
    margin: 4em 0;
  }
}

/**
 * Featured Images
 */
.img-featured {
  margin: 0 0 2em 0;
  width: 100%;
  height: auto;
}

/**
 * Comments
 */
.comments-wrapper {
  margin: 2em 0;
}

@media (min-width: 760px) {
  .comments-wrapper {
    margin: 6em 0;
  }
}

/*==============================================================================
  @Blocks - scss/custom/_custom.scss
==============================================================================*/
/**
 * Action Block
 */
.block-action {
  max-width: 50em;
  margin: 1.5em auto;
  padding: 1.5em 3em;
  background-image: linear-gradient(45deg, #880E4F, #311B92);
  border-radius: 3px;
  color: #fff;
  text-align: center;
}

.block-action h2 {
  color: #fff;
}

.block-action .action {
  margin: 1em 0;
  display: block;
}

.block-action .action .button {
  width: 100%;
  display: block;
  margin: 1em 0;
  background: rgba(255, 255, 255, 0);
  border: 2px solid white;
  color: #fff;
  transition: none;
}

.block-action .action .button:hover, .block-action .action .button:focus {
  background: #fff;
  color: #212121;
}

@media (min-width: 760px) {
  .block-action {
    margin: 3em auto;
  }
  .block-action .action {
    display: inline-block;
  }
  .block-action .action .button {
    width: auto;
    display: inline-block;
    margin: 0 0.5em;
  }
}

/*==============================================================================
  @Button-totop - scss/custom/_custom.scss
==============================================================================*/
.button-totop {
  opacity: 0;
  transition: all 0.25s linear;
}

.button-totop.fadeIn {
  opacity: 1;
}

/*==============================================================================
  @Footer - scss/custom/_custom.scss
==============================================================================*/
.footer {
  padding: 2em 0;
  font-size: 0.875em;
  color: #9E9E9E;
  text-align: center;
}

.footer .logo {
  border: 0 none;
}

.footer img {
  display: inline-block;
  opacity: 0.4;
}

.footer a:not(.button) {
  color: #9E9E9E;
}

.footer a:not(.button):hover {
  color: #1976D2;
}

.footer .button {
  margin: 1em auto 0;
  background: #fff;
}

.footer .button:hover {
  color: #2196F3;
}

@media (min-width: 760px) {
  .footer {
    text-align: left;
  }
  .footer .logo {
    position: relative;
    top: -3px;
    float: right;
  }
  .footer img {
    margin: 0;
  }
  .footer .button-totop {
    display: none;
  }
}

/*==============================================================================
  @End - scss/custom/_custom.scss
==============================================================================*/
